import React, { useState } from 'react';
import styles from './PropBetListItem.module.css';

interface Props {
    id : number;
    expanded : boolean;
}

const PropListBody: React.FC<Props> = (props) => {
    let classes = styles.container + styles.body;
    if (props.expanded) {
        classes = styles.container + styles.body.expanded
    }
    return <div className={classes}></div>
};

export default PropListBody;